import TabBar from "@/components/TabBar";
import loading from "@/components/loading";
import { getUserInfo, basicSetup } from "@/api";
export default {
  components: {
    TabBar,
    loading
  },
  data() {
    return {
      statusBarHeight: uni.getStorageSync("statusBarHeight"), // 兼容安卓通知栏
      userInfo: {}, // 用户信息
      avatarModelShow: false, //更换用户头像弹框
      nameModelShow: false, // 更改用户昵称弹框
      loadingShow: false, // loading是否显示
      uname: "" // 修改用户昵称表单数据
    };
  },
  computed: {
    day() {
      // 用户注册账号天数
      let dateSpan, tempDate, iDays, sDate1, sDate2;
      sDate1 = new Date(uni.getStorageSync("USERINFO").creationTime);
      sDate2 = new Date();
      dateSpan = sDate2 - sDate1;
      console.log(dateSpan);
      dateSpan = Math.abs(dateSpan);
      iDays = Math.ceil(dateSpan / (24 * 3600 * 1000));
      return iDays;
    }
  },
  onLoad() {
    this.loadingShow = true;
    this.getUserInfo();
  },
  methods: {
    chooseImage() {
      // 更改头像
      uni.chooseImage({
        success: chooseImageRes => {
          const tempFilePaths = chooseImageRes.tempFilePaths;
          uni.uploadFile({
            url: this.$HTTP + "/user/uploadFile", //仅为示例，非真实的接口地址
            filePath: tempFilePaths[0],
            name: "file",
            success: uploadFileRes => {
              this.loadingShow = true;
              if (uploadFileRes.statusCode == 200) {
                let data = {
                  uid: this.userInfo.uid,
                  avatar: JSON.parse(uploadFileRes.data).filePath,
                  uname: this.userInfo.uname
                };
                this.avatarModel();
                this.setUserInfo();
              }
            }
          });
        }
      });
    },
    setName() {
      // 修改昵称
      if (!this.uname) {
        uni.showToast({
          title: "请将数据填写完成", //提示的内容,
          icon: "none", //图标,
          duration: 1500 //延迟时间,
        });
        return;
      }
      let data = {
        uid: this.userInfo.uid,
        avatar: this.userInfo.avatar,
        uname: this.uname
      };
      this.loadingShow = true;
      this.setUserInfo(data);
      this.nameModel();
    },
    setUserInfo(data) {
      // 修改用户信息
      basicSetup(data).then(res => {
        uni.setStorageSync("UID", res.msg);
        data = {
          uid: uni.getStorageSync("UID")
        };
        // 获取用户信息
        getUserInfo(data).then(res => {
          console.log(res);
          if (res.ok) {
            uni.setStorageSync("USERINFO", res.data);
            this.getUserInfo();
            this.loadingShow = false;
            wx.showToast({
              title: "更改成功", //提示的内容,
              icon: "none", //图标,
              duration: 1200 //延迟时间,
            });
          }
        });
      });
    },
    getUserInfo() {
      // 更新用户信息
      this.userInfo = uni.getStorageSync("USERINFO") || {};
      this.loadingShow = false;
    },
    avatarModel() {
      // 更换用户头像弹框控制
      this.avatarModelShow = !this.avatarModelShow;
    },
    nameModel() {
      // 更换用户头像弹框控制
      this.nameModelShow = !this.nameModelShow;
      this.uname = "";
    },
    stop() {
      // 停止冒泡函数
      return;
    }
  }
};
